import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { UserBankInfoService } from 'src/app/service/user-bank-info.service';
declare var swal: any;


@Component({
  selector: 'app-user-bankinfo-add',
  templateUrl: './user-bankinfo-add.component.html'
})
export class UserBankinfoAddComponent implements OnInit {

  loadingState = false;
  addForm: FormGroup;
  selectedFile: File;
  editImageUrl: any = null;
  paymentTypeList:any=[];
  showLoader = false;
  formErrors = {
    account_holder_name: null,
    apierror: null
  };
  isEditing = false;
  editId = null;


  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private fb: FormBuilder,
    private UserBankInfoService: UserBankInfoService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.isEditing = true;
      this.editId = this.activatedRoute.snapshot.paramMap.get('id');
      this.editObject();
    }
    this.addForm = fb.group({
      payment_type: [1],
      account_holder_name: [null, Validators.compose([Validators.required])],
      bank_name: [null, Validators.compose([Validators.required])],
      account_number: [null, Validators.compose([Validators.required])],
      bank_ific: [null, Validators.compose([Validators.required])],
      bank_address: [null, Validators.compose([Validators.required])],
    });
  }

  ngOnInit() {

  }

  editObject() {
    this.loadingState = true;
    this.UserBankInfoService.getUserBankInfoById(this.editId).subscribe((response) => {
      this.loadingState = false;
      if (response.data) {
        this.editImageUrl = response.data.image,
          this.addForm.patchValue({
            payment_type: response.data.payment_type,
            account_holder_name: response.data.account_holder_name,
            bank_name: response.data.bank_name,
            account_number: response.data.account_number,
            bank_ific: response.data.bank_ific,
            bank_address: response.data.bank_address
          });
      }
    }, (error) => {
      this.loadingState = false;
      this.router.navigateByUrl('/bankinfo');
    });
  }

  fileChangeEvent(fileInput: any) {
    const reg = /(.*?)\.(jpg|jpeg|png|gif|giff)$/;
    if (!fileInput.target.files[0].account_holder_name.match(reg)) {
      swal.fire(
        'Invalid File!',
        'Please select valid file ',
        'error'
      );
      this.removeFile();
      return false;
    } else {
      this.removeFile();
      this.selectedFile = fileInput.target.files[0];
    }
  }

  removeFile() {
    this.selectedFile = null;
  }

  submitForm(formdata): void {
    if (this.addForm.valid) {
      this.showLoader = true;
      if (this.isEditing) {
        this.UserBankInfoService.editUserBankInfo(this.editId, formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/bankinfo');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('account_holder_name')) {
                this.formErrors.account_holder_name = obj.account_holder_name;
              }

            });
          }
        });
      } else {
        this.UserBankInfoService.addUserBankInfo(formdata.value).subscribe((response) => {
          this.showLoader = false;
          if (response.success) {
            this.router.navigateByUrl('/bankinfo');
          } else {
            this.showLoader = false;
            response.error.map(obj => {

              if (obj.hasOwnProperty('account_holder_name')) {
                this.formErrors.account_holder_name = obj.account_holder_name;
              }
            });
          }
        });
      }
    }
  }
}
